@font-face {
	font-family: "georgia-bold";
	src: url("../fonts/georgia-bold.woff") format("woff"),
	url("../fonts/georgia-bold.woff2") format("woff2") {}
	font-weight: bold;
	font-style: normal; }

@font-face {
	font-family: "helvetica-neue-light";
	src: url("../fonts/helvetica-neue-light.woff") format("woff"),
	url("../fonts/helvetica-neue-light.woff2") format("woff2") {}
	font-weight: lighter;
	font-style: normal; }

@font-face {
	font-family: "helvetica-neue-regular";
	src: url("../fonts/helvetica-neue-regular.woff") format("woff"),
	url("../fonts/helvetica-neue-regular.woff2") format("woff2") {}
	font-weight: normal;
	font-style: normal; }

@font-face {
	font-family: "helvetica-neue-bold";
	src: url("../fonts/helvetica-neue-bold.woff") format("woff"),
	url("../fonts/helvetica-neue-bold.woff2") format("woff2") {}
	font-weight: bold;
	font-style: normal; }

@font-face {
  font-family: "open-sans-regular";
  src: url('../fonts/OpenSans-Regular-webfont.eot');
  src: url('../fonts/OpenSans-Regular-webfont.eot?#iefix') format('embedded-opentype'),
  url('../fonts/OpenSans-Regular-webfont.woff') format('woff'),
  url('../fonts/OpenSans-Regular-webfont.ttf') format('truetype'),
  url('../fonts/OpenSans-Regular-webfont.svg#open_sansregular') format('svg') {}
  font-weight: normal;
  font-style: normal; }
