body {
	font-family: $ff-main;
	line-height: 1.2;
	font-size: 1rem; }

*:hover {
	transition: 0.3s; }

*, *:focus {
	outline: 0; }

html {
	box-sizing: border-box; }

*, *:after, *:before {
	box-sizing: inherit; }

ul, ol {
	list-style-type: none; }

.clearfix {
	&:after {
		content: '';
		display: table;
		clear: both; } }

a {
	text-decoration: none; }

* {
	margin: 0;
	padding: 0; }
