.hero {
	&-wrapper {
		background-image: url("../images/hero-bg.jpg");
		background-repeat: no-repeat;
		background-size: cover;
		background-position-x: center;
		background-position-y: center; }

	padding: 175px 0 236px 0;
	text-align: center;
	&__title {
		font-size: 48px;
		color: $fc-main-light;
		font-family: $ff-main-bold; }
	&__description {
		font-size: 30px;
		margin-bottom: 25px;
		color: $fc-main-light;
		font-family: $ff-main-light; } }
