.header {
	&__top {
		background-color: #f0f2f4;
		border-bottom: 1px solid #e2e2e2;
		.contacts {
			text-align: right;
			a {
				display: inline-block;
				vertical-align: middle; }
			.contact {
				padding: 10px 25px 15px 20px;
				color: $fc-main-dark;
				&:hover {
					color: lighten($fc-main-dark, 30%); }
				i {
					font-size: 1.1rem;
					margin-right: 10px;
					vertical-align: middle; }
				&__text {
					font-size: 15px;
					vertical-align: middle; } }
			.contact:first-child {
				border-right: 1px solid #e2e2e2; }
			.facebook-icon {
				width: 25px;
				height: 25px;
				background-color: #1287bf;
				border-radius: 4px;
				color: #fff;
				position: relative;
				i {
					@include center;
					font-size: 0.9rem; }
				&:hover {
					background-color: darken(#1287bf, 10%); } } } }

	&__bottom {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding: 40px 0;
		background-color: #fff;
		.logo {
			img {
				width: 241px;
				height: 51px; } } }
	&__button {
		padding: 13px 23px; } }
.nav {
	&__link {
		display: inline-block;
		padding: 15px;
		font-family: $ff-main-bold;
		text-transform: capitalize;
		color: $fc-main-dark;
		position: relative;
		&:hover {
			color: #de2b24; }
		&--active {
			color: #de2b24; }
		&:after {
			content: "";
			display: inline-block;
			width: 5px;
			height: 5px;
			background-color: #424242;
			border-radius: 2px;
			position: absolute;
			top: 50%;
			transform: translateY(-50%);
			right: -2.5px; }
		&:last-child:after {
			display: none; } }

	&__burger {
		display: none;
		width: 50px;
		height: 50px;
		position: fixed;
		top: 0;
		right: 0;
		margin-right: 10px;
		margin-top: 10px;
		cursor: pointer;
		&_active {
			.nav__burger-line {
				height: 0;
				transition-duration: 0s;
				&:after {
					@include centerY;
					transform: rotate(-45deg); }
				&:before {
					@include centerY;
					transform: rotate(45deg); } } } }
	&__burger-line {
		width: 50px;
		height: 5px;
		background-color: #424242;
		margin-top: 22.5px;
		&:after {
			content: "";
			display: block;
			width: 50px;
			height: 5px;
			position: absolute;
			background-color: #424242;
			bottom: 0px; }
		&:before {
			content: "";
			display: block;
			width: 50px;
			height: 5px;
			position: absolute;
			background-color: #424242;
			top: 0px; } } }

.header_fixed {
	width: 100%;
	z-index: 99;
	background-color: rgba(#fff, 0.5); }
.header__top_fixed {
	display: none; }
.header__bottom_fixed {
	padding: 15px;
	background-color: transparent; }
