.form-section {
	background-color: #1287bf;
	padding-top: 90px;
	padding-bottom: 110px;
	&__title {
		margin-bottom: 0; }
	&__button-down {
		display: block;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 50px;
		&:hover {
			border-color: lighten(#1287bf, 30%); } }

	.form {
		background-color: #fff;
		padding: 50px 60px 68px 64px;
		position: relative;
		width: 66.66666%;
		margin-right: auto;
		margin-left: auto;
		&__text {
			font-size: 14.5px;
			color: $fc-form-main;
			text-align: center;
			margin-bottom: 40px; }
		&__inputs {
			margin-bottom: 35px; }
		&__input {
			padding: 20px;
			width: 80%;
			display: block;
			margin-right: auto;
			margin-left: auto;
			margin-bottom: 8px;
			border-radius: 5px;
			border: 1px solid #d8d8d8;
			font-family: $ff-main;
			font-size: 14px;
			color: $fc-form-main;
			&:focus {
				border: 1px solid #1287bf;
				box-shadow: 0px 0px 3.5px 0px #1287bf; } }
		&__submite {
			margin-right: auto;
			margin-left: auto;
			display: block;
			padding: 15px 30px;
			text-transform: uppercase;
			background-color: #de2b24;
			color: #fff; }
		&__button-close {
			width: 49px;
			height: 49px;
			border-radius: 50%;
			border: 2px solid #fff;
			background-color: #de2b24;
			position: absolute;
			right: -24px;
			top: -24px;
			font-size: 20px;
			text-align: center;
			line-height: 49px;
			color: #fff; }
		&:after {
			content: "";
			display: block;
			border-color: #fff transparent;
			border-style: solid;
			border-width: 0px 32px 32px 32px;
			height: 0px;
			width: 0px;
			@include centerX;
			top: -32px; } } }
