.work {
	padding-top: 105px;
	padding-bottom: 127px;
	background-color: #de2b24;
	&__list {
		display: flex;
		flex-wrap: wrap; }
	&__item {
		width: 25%;
		&--name {
			background-color: #f35e59;
			position: relative;
			text-align: center;
			padding-top: 70px;
			color: $fc-main-light;
			&:before {
				display: block;
				content: "";
				@include sprite($work-item);
				@include centerX; } } }
	&__name {
		margin-top: 65px;
		font-size: 26px; }
	&__description {
		font-size: 16px; }
	&__picture {
		width: 100%;
		height: 100%; } }
