.about {
	padding-top: 90px;
	padding-bottom: 120px;
	color: $fc-main-dark;
	&__list {
		display: flex;
		justify-content: space-around; }
	&__item {
		width: 25%;
		text-align: center; }
	&__header {
		font-size: 22px;
		font-family: $ff-main-bold;
		margin-bottom: 10px; }
	&__description {
		font-family: $ff-paragraphs;
		font-size: 13.5px;
		line-height: 1.5rem; }
	.icon {
		margin-bottom: 23px;
		display: inline-block; }
	.icon-like {
		@include sprite($like); }
	.icon-book {
		@include sprite($book); }
	.icon-message {
		@include sprite($message); } }
