.container {
	// max-width: 1024px
	margin: auto; }

// Buttons

.button {
	border-radius: 3px;
	border-width: 0px;
	font-family: $ff-main-bold; }

.button--red {
	background-color: #de2b24;
	color: #fff;
	font-size: 15px;
	&:hover {
		background-color: lighten(#de2b24, 10%); } }

.button--yellow {
	background-color: #f8cd53;
	padding: 21px 41px;
	border-radius: 5px;
	text-transform: uppercase;
	font-size: 23px;
	font-family: $ff-main-bold;
	color: $fc-main-light; }

.button-down {
	width: 64px;
	height: 64px;
	border-radius: 50%;
	border: 3px solid #fff;
	background-color: transparent;
	position: relative;
	i {
		@include center;
		margin-left: 1px;
		margin-top: 2px;
		font-size: 30px;
		color: #fff; } }

// Title

.title {
	text-align: center;
	padding-bottom: 15px;
	margin-bottom: 45px;
	&__header {
		font-size: 36px;
		font-family: $ff-main-bold;
		margin-bottom: 3px;
		text-transform: capitalize; }
	&__text {
		font-family: $ff-main-light;
		font-size: 20px; } }

.title--with-bottom-line {
	position: relative;
	&:after {
		content: "";
		display: block;
		@include centerX;
		width: 47px;
		height: 5px;
		background-color: #000;
		bottom: 0; } }

.title--with-bottom-line.title--dark {
	&:after {
		background-color: $fc-main-dark; } }

.title--with-bottom-line.title--light {
	&:after {
		background-color: $fc-main-light; } }
.title--dark {
	color: $fc-main-dark; }
.title--light {
	color: $fc-main-light; }
