// MAX 1084px

@media (max-width: 1084px) {
	body {} }

// MAX 992px

@media (max-width: 992px) {
	// HEADER
	.header {
		&__top {
			.contacts {
				text-align: center; } }
		&__bottom {
			flex-direction: column; } }

	// WORK
	.work {
		&__item {
			width: 50%;
			margin-bottom: 1px;
			&--name {
				padding-top: 35px; } }
		&__name {
			font-size: 22px; }
		&__description {
			font-size: 14px; } } }

// MAX 768px

@media (max-width: 768px) {
	// HERO
	.hero {
		padding: 100px 0px 100px 0; }

	// ABOUT
	.about {
		padding: 50px 0px 50px 0;
		&__list {
			flex-direction: column;
			align-items: center; }
		&__item {
			width: 60%;
			margin-bottom: 40px;
			&:last-child {
				margin-bottom: 0; } }
		.icon {
			margin-bottom: 5px; }
		&__header {
			margin-bottom: 0px; } }

	// APPROCH
	.approch {
		padding: 50px 0px 50px 0;
		&__step {
			display: none; }
		&__text {
			padding: 20px; } }

	// WORK
	.work {
		padding: 50px 0px; }

	// FORM SECTION
	.form-section {
		.form {
			width: 90%;
			padding: 20px;
			&__input {
				width: 90%; } } } }


// MAX 576px

@media (max-width: 576px) {
	// COMMON
	.container {
		padding: 0px 15px; }

	.title {
		&__header {
			font-size: 30px; }
		&__text {
			font-size: 18px; } }

	// HEADER
	.header {
		&__top {
			.contacts {
				.contact {
					display: block;
					border-bottom: 1px solid #e2e2e2; }
				.facebook-icon {
					margin: 10px 0px; } } }
		&__bottom {
			display: none;
			&_active {
				display: flex !important; } } }
	.nav {
		&__burger {
			display: block; } }

	// HERO
	.hero {
		&__title {
			font-size: 30px; }
		&__description {
			font-size: 20px; }
		.button--yellow {
			font-size: 16px;
			padding: 18px 35px; } }
	.nav {
		&__link {
			display: block;
			margin: auto;
			text-align: center;
			&::after {
				display: none; } } }

	// APPROCH
	.approch {
		&__block, &__block:nth-child(even), &__block:nth-child(odd) {
			flex-direction: column;
			border-radius: 10px;
			img {
				border-top-right-radius: 10px;
				border-top-left-radius: 10px; } }
		&__img {
			width: 100%; }
		&__text {
			text-align: center;
			width: 100%; } } }
