.footer {
	background-color: #2a2a2a;
	&__top {
		padding-top: 85px;
		padding-bottom: 80px;
		text-align: center; }
	&__title {
		font-size: 29px;
		line-height: 22px;
		color: $fc-main-light;
		font-family: $ff-main-bold;
		margin-bottom: 27px; }
	&__text {
		font-size: 14px;
		line-height: 22px;
		color: $fc-main-light;
		font-family: $ff-paragraphs;
		margin-bottom: 26px; }
	&__social-list {
		display: block;
		margin-bottom: 30px; }
	&__social-item {
		width: 49px;
		height: 49px;
		border: 2px solid #515151;
		border-radius: 3px;
		display: inline-flex;
		justify-content: center;
		align-items: center;
		margin: 0px 10px;
		i {
			font-size: 23px;
			color: $fc-main-light; }
		&:hover {
			cursor: pointer;
			border-color: #de2b24; } }
	&__input {
		width: 66.66666%;
		height: 54px;
		background-color: #515151;
		border-width: 0px;
		display: block;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 27px;
		padding-left: 20px;
		color: #cecece;
		font-family: $ff-paragraphs;
		@include placeholder {
			font-family: $ff-paragraphs;
			color: #cecece;
			font-size: 14px; } }
	&__submite {
		background-color: transparent;
		border: 2px solid #515151;
		padding: 15px 33px;
		color: #fff;
		font-size: 17px;
		font-family: $ff-main-bold;
		&:hover {
			cursor: pointer;
			background-color: #515151; } }

	&__bottom {
		background-color: #515151;
		padding: 23px 0px; }
	&__copy {
		font-size: 14px;
		text-align: center;
		color: $fc-main-lighten-dark;
		font-family: $ff-paragraphs; } }
