.approch {
	background-color: #f3f3f3;
	padding-top: 80px;
	padding-bottom: 130px;
	&__blocks {}
	&__block {
		display: flex;
		margin-bottom: 40px;
		background-color: #fff;
		color: $fc-main-dark;
		border-radius: 4px;
		&:nth-child(even) {
			flex-direction: row-reverse;
			text-align: right;
			img {
				border-top-right-radius: 4px;
				border-bottom-right-radius: 4px; } }
		&:nth-child(odd) {
			text-align: left;
			img {
				border-top-left-radius: 4px;
				border-bottom-left-radius: 4px; } } }
	&__img {
		width: 33.333%;
		position: relative;
		img {
			width: 100%;
			height: 100%; } }
	&__text {
		width: 66.666%;
		padding-left: 88px;
		padding-right: 70px;
		display: flex;
		margin-top: auto;
		margin-bottom: auto;
		flex-direction: column; }
	&__header {
		font-family: $ff-main-bold;
		font-size: 18px;
		line-height: 26px;
		margin-bottom: 5px; }
	&__description {
		font-family: $ff-paragraphs;
		font-size: 13.5px;
		line-height: 22px; } }

.approch {
	&__step {
		display: block;
		@include centerY;
		width: 106px;
		height: 106px;
		background-color: #de2b24;
		border: 2px solid #fff;
		border-radius: 5px;
		font-size: 20px;
		font-family: $ff-main;
		text-align: center;
		color: #ffffff;
		line-height: 30px;
		padding-top: 20px; }
	&__number {
		font-size: 60px;
		display: block;
		font-family: $ff-steps;
		margin-bottom: 7px; } }

.approch__block:nth-child(odd) .approch__step {
	right: -53px; }
.approch__block:nth-child(even) .approch__step {
	left: -53px; }
